import { SOUND_SET_VOLUM, SOUND_SET_TRACK } from '../actionTypes';

const initialState = {
  volumeValue: 10,
  track: ``,
  loop: true
};

export default (state = initialState, action) => {
  switch (action.type) {
    case SOUND_SET_VOLUM:
      return {
        ...state,
        volumeValue: action.volumeValue
      };
    case SOUND_SET_TRACK:
      return {
        ...state,
        track: action.track,
        loop: action.loop
      };
    default:
      return state;
  }
};
