import React from 'react';
import PropTypes from 'prop-types';

import Lines from 'components/EditorComponents/Lines/Lines.js';
import Message from 'components/EditorComponents/Message/Message.js';
import Signal from 'components/EditorComponents/Signal/Signal.js';
import Box from 'components/EditorComponents/Box/Box.js';
import Speaker from 'components/EditorComponents/Speaker/Speaker.js';
import Meta17820 from 'components/EditorComponents/Meta17820/Meta17820.js';
import Meta18580 from 'components/EditorComponents/Meta18580/Meta18580.js';

import classes from './Editor.module.scss';

class Editor extends React.Component {
  getSchemeItem = item => {
    const itemType = {
      lines: (
        <Lines
          key={item.id}
          lines={item.lines}
          onChange={this.props.onEditorChange}
        />
      ),
      message: <Message key={item.id} item={item} />,
      signal: (
        <Signal
          key={item.id}
          item={item}
          onChange={this.props.onEditorChange}
        />
      ),
      box: <Box key={item.id} item={item} />,
      speaker: <Speaker key={item.id} item={item} />,
      meta17820: (
        <Meta17820
          key={item.id}
          item={item}
          LCDprops={this.props.scheme.meta17820}
          onChange={this.props.onEditorChange}
        />
      ),
      meta18580: (
        <Meta18580
          key={item.id}
          item={item}
          onChange={this.props.onEditorChange}
        />
      )
    };

    return itemType[item.type];
  };
  render() {
    if (!this.props.scheme) return; // если схемы нет ничего не рисуем
    const lineStyle = {
      width: this.props.scheme.settings.width,
      height: this.props.scheme.settings.height
    };

    const message = [];
    // eslint-disable-next-line
    for (let key in this.props.scheme.message) {
      message.push(this.props.scheme.message[key]);
    }

    return (
      <div className={classes.main} style={lineStyle}>
        {this.props.scheme &&
          this.props.scheme.elements.map(it => this.getSchemeItem(it))}
        {message.map(it => this.getSchemeItem(it))}
      </div>
    );
  }
}

Editor.propTypes = {
  scheme: PropTypes.object,
  onEditorChange: PropTypes.func
};

export default Editor;
