import React from 'react';
import history from 'routers/history.js';

import LayoutPage from 'layout/LayoutPage/LayoutPage.js';

import ButtonBase from '@material-ui/core/ButtonBase';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

import meta17821Img from 'assets/img/components/17821_m.png';
import calculatorImg from 'assets/img/page/calculator.png';

import { ROUTER_LINK } from 'utils/const/routerLink.js';

import classes from './Home.module.scss';

class Home extends React.Component {
  constructor(props) {
    super(props);

    const steps = [
      {
        title: `Системы речевого оповещения при пожаре`,
        textBody1: `алгоритм работы МЕТА 17820 / МЕТА 17821`,
        img: meta17821Img,
        imgTitle: `оповещение при пожаре`,
        imgAlt: `оповещение при пожаре`,
        linkTo: ROUTER_LINK.meta17820
      },
      {
        title: `Конфигуратор`,
        // textBody1: `проверка правильности выбора оповещателя`,
        // img: calculatorImg,
        linkTo: ROUTER_LINK.configurator
      },
      {
        title: `Расчета уровня звукового давления`,
        textBody1: `проверка правильности выбора оповещателя`,
        img: calculatorImg,
        linkTo: ROUTER_LINK.calcSound
      }
    ];

    this.state = {
      steps
    };
  }
  onButtonBaseClick = indexBtn => {
    history.push(this.state.steps[indexBtn].linkTo);
  };

  render() {
    return (
      <LayoutPage>
        <div className={classes.stepBlock}>
          {this.state.steps.map((it, i) => {
            // если картинки нет стили убирают сдвиг для картинки
            const cls = [
              classes.PaperBlock,
              it.img ? null : classes.PaperBlockFull
            ];

            return (
              <ButtonBase
                focusRipple
                key={it.title}
                className={classes.step_item}
                onClick={() => this.onButtonBaseClick(i)}
              >
                {it.img && (
                  <div className={classes.item_img}>
                    <img
                      className={classes.img}
                      src={it.img}
                      title={it.imgTitle}
                      alt={it.imgAlt}
                    />
                  </div>
                )}
                <Paper className={cls.join(` `)}>
                  <Typography variant="h2" gutterBottom>
                    {it.title}
                  </Typography>
                  <Typography variant="body1">{it.textBody1}</Typography>
                </Paper>
              </ButtonBase>
            );
          })}
        </div>
      </LayoutPage>
    );
  }
}

export default Home;
