import React from 'react';
import { connect } from 'react-redux';
import { setSirene } from 'store/actions/sirene';
import LayoutPage from 'layout/LayoutPage/LayoutPage.js';
import { Typography } from '@material-ui/core';
import ActivePaperBtn from 'components/UI/ActivePaperBtn/ActivePaperBtn';
import PanelMeta17820 from './PanelMeta17820/PanelMeta17820';
import classes from './PageConfigurator.module.scss';

import meta17821Img from 'assets/img/components/17821_m.png';

class PageConfigurator extends React.Component {
  constructor(props) {
    super(props);

    const blocks = [
      {
        title: `МЕТА 17820`,
        img: meta17821Img,
        imgTitle: `МЕТА 17820/17821`,
        imgAlt: `МЕТА 17820/17821`
      },
      {
        title: `МЕТА 17821`,
        img: meta17821Img,
        imgTitle: `МЕТА 17820/17821`,
        imgAlt: `МЕТА 17820/17821`
      }
    ];

    this.state = {
      activeBlock: `МЕТА 17820`,
      blocks
    };
  }

  onActiveBlockChange = activeBlock => {
    this.setState({
      activeBlock
    });
  };

  render() {
    const { activeBlock, blocks } = this.state;
    const showMEta17820 =
      activeBlock === 'МЕТА 17820' || activeBlock === 'МЕТА 17821';

    return (
      <LayoutPage>
        <Typography variant="h1" gutterBottom>
          Конфигуратор
        </Typography>
        <p className={classes.blockTitle}>Выберите блок</p>

        <div className={classes.blockStep}>
          {blocks.map(it => (
            <ActivePaperBtn
              key={it.title}
              isActive={activeBlock === it.title}
              {...it}
              width="180px"
              onChange={this.onActiveBlockChange}
            />
          ))}
        </div>

        {showMEta17820 && <PanelMeta17820 activeBlock={activeBlock} />}
      </LayoutPage>
    );
  }
}

const mapStateToProps = state => ({
  sirene: state.sirene.sirene
});

const mapDispatchToProps = {
  setSirene
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PageConfigurator);
