import React from 'react';
import { ButtonBase, Typography } from '@material-ui/core';
import classes from './ActivePaperBtn.module.scss';

const ActivePaperBtn = ({
  isActive,
  title,
  blockName,
  img,
  imgTitle,
  imgAlt,
  width,
  onChange
}) => {
  const cls = isActive ? [classes.paper, classes.paperActive] : [classes.paper];

  return (
    <div className={classes.stepBlock} style={{ width: width }}>
      <ButtonBase
        focusRipple
        className={classes.step_item}
        onClick={() => onChange && onChange(blockName || title)}
      >
        <div className={cls.join(' ')}>
          <div className={classes.item_img}>
            {img && (
              <img
                className={classes.img}
                src={img}
                title={imgTitle}
                alt={imgAlt}
              />
            )}
          </div>
          <Typography variant="body1">{title}</Typography>
        </div>
      </ButtonBase>
    </div>
  );
};

export default ActivePaperBtn;
