import React from 'react';
import PropTypes from 'prop-types';

import LCDLine0 from './LCDLine0/LCDLine0.js';
import LCDLine1 from './LCDLine1/LCDLine1.js';
import LCDLine2 from './LCDLine2/LCDLine2.js';
import LCDLine3 from './LCDLine3/LCDLine3.js';

import classes from './LCDMeta17820.module.scss';

/**
 Отображение на дисплее LCD оттаклик
 scheme.meta17820
 */

class LCDMeta17820 extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      LCDTextLines: [null, null, null, null]
    };
  }

  componentDidMount() {
    this.setLCDTextLines(this.props.LCDprops);
  }
  componentDidUpdate(prevProps) {
    if (prevProps.LCDprops !== this.props.LCDprops) {
      this.setLCDTextLines(this.props.LCDprops);
    }
  }
  setLCDTextLines(meta17820) {
    const { LCDTextLines } = this.state;

    const textForLine2 = {
      fire: `ПOЖАР                         `, // пожар
      goI4S: `ГО и ЧС                       `, // ГО и ЧС
      pultSignal: `ПУЛЬТ                         `, // управление с пульта
      problem: `НЕИСПРАВНОСТЬ                 `, // неисправность
      power: `НЕИСПРАВНОСТЬ                 `, // питание
      offZummer: `ОТКЛ ЗУМЕРРА                  `, // откл зумерра
      offVolume: `ОТКЛ ЗВУКА                    `, // откл звука
      def: `АКТИВНОСТИ НЕТ                `
    };
    const line1 = meta17820.pultSignal
      ? textForLine2.pultSignal
      : meta17820.fire
      ? textForLine2.fire
      : meta17820.goI4S
      ? textForLine2.goI4S
      : meta17820.problem
      ? textForLine2.problem
      : textForLine2.def;

    const line4Arr = [`АКТ.:`];
    if (meta17820.activeZone) {
      meta17820.activeZone.forEach(it => {
        line4Arr.push(it ? ` +` : ` -`);
      });
    }

    // const line4 = `АКТ.: - - - - - - - -         `;
    const line3 = line4Arr.join(``);

    LCDTextLines[1] = line1;
    LCDTextLines[3] = line3;

    this.setState({ LCDTextLines });
  }
  render() {
    return (
      <div className={classes.main}>
        <div className={classes.text}>
          <LCDLine0 textLine={this.state.LCDTextLines[0]} />
          <LCDLine1 textLine={this.state.LCDTextLines[1]} />
          <LCDLine2 textLine={this.state.LCDTextLines[2]} />
          <LCDLine3 textLine={this.state.LCDTextLines[3]} />
        </div>
      </div>
    );
  }
}

LCDMeta17820.propTypes = {
  LCDprops: PropTypes.object.isRequired
};

export default LCDMeta17820;
