import React from 'react';
import { useDispatch } from 'react-redux';
import { setSirene } from 'store/actions/sirene';
import sirensList from 'utils/const/list/sirensList';
import { ButtonBase } from '@material-ui/core';
import Select from 'components/UI/Select/Select';
import classes from './SelectSiren.module.scss';

const getList = (arr, field) => [...new Set(arr.map(it => it[field]))];

const getTypeSirens = (state, field) => {
  if (!state) {
    return getList;
  }
  let newArr = [...sirensList];

  if (state.type && field !== 'type') {
    newArr = newArr.filter(it => it.type === state.type);
  }
  if (state.inputPower && field !== 'inputPower') {
    newArr = newArr.filter(it => it.inputPower === state.inputPower);
  }
  if (state.sensitivity && field !== 'sensitivity') {
    newArr = newArr.filter(it => it.sensitivity === state.sensitivity);
  }

  return getList(newArr, field);
};

const initState = {
  type: ``,
  typeList: getList(sirensList, 'type'),
  inputPower: ``,
  inputPowerList: getList(sirensList, 'inputPower'),
  sensitivity: ``,
  sensitivityList: getList(sirensList, 'sensitivity')
};

const SelectSiren = ({ handleCancelBtnClick }) => {
  const dispatch = useDispatch();
  const [values, setValues] = React.useState(initState);

  const handleSelectChange = (e, fieldName) => {
    setValues(oldValues => {
      const newState = {
        ...oldValues,
        [fieldName]: e.target.value
      };
      const typeList = getTypeSirens(newState, 'type');
      const inputPowerList = getTypeSirens(newState, 'inputPower');
      const sensitivityList = getTypeSirens(newState, 'sensitivity');

      const type = typeList.length === 1 ? typeList[0] : newState.type;
      const inputPower =
        inputPowerList.length === 1 ? inputPowerList[0] : newState.inputPower;
      const sensitivity =
        sensitivityList.length === 1
          ? sensitivityList[0]
          : newState.sensitivity;

      return {
        type,
        typeList,
        inputPower,
        inputPowerList,
        sensitivity,
        sensitivityList
      };
    });
  };

  const handleResetClick = () => setValues(() => initState);
  const handleSetSireneClick = () => {
    if (!(values.type && values.inputPower && values.sensitivity)) {
      return;
    }
    dispatch(
      setSirene({
        type: values.type,
        inputPower: values.inputPower,
        sensitivity: values.sensitivity
      })
    );
  };

  return (
    <div className={classes.main}>
      <p className={classes.title}>Выбирете параметры оповещателя</p>

      <div className={classes.panelSelect}>
        <div className={classes.selectItem}>
          <Select
            title="Тип"
            value={values.type}
            valueList={values.typeList}
            fieldName="type"
            onChange={handleSelectChange}
          />
        </div>
        <div className={classes.selectItem}>
          <Select
            title="Чувствительность"
            value={values.inputPower}
            valueList={values.inputPowerList}
            fieldName="inputPower"
            onChange={handleSelectChange}
          />
        </div>
        <div className={classes.selectItem}>
          <Select
            title="Электрическая мощность"
            value={values.sensitivity}
            valueList={values.sensitivityList}
            fieldName="sensitivity"
            onChange={handleSelectChange}
          />
        </div>
        <div className={classes.selectItem}>
          <ButtonBase
            focusRipple
            className={classes.resetBtn}
            onClick={handleResetClick}
          >
            <p>Сброс</p>
          </ButtonBase>
        </div>
      </div>

      <div className={classes.panelBtn}>
        <ButtonBase
          focusRipple
          className={classes.setSirensBtn}
          onClick={handleSetSireneClick}
        >
          <p>Выбрать тип оповещетеля</p>
        </ButtonBase>

        {handleCancelBtnClick && (
          <ButtonBase
            focusRipple
            className={classes.cancelBtn}
            onClick={handleCancelBtnClick}
          >
            <p>Закрыть панель выбора оповещателя</p>
          </ButtonBase>
        )}
        {/* old show table
      <div className={classes.rowHeader}>
        <p className={classes.cell1}>Тип оповещателя</p>
        <p className={classes.cell2}>N0</p>
        <p className={classes.cell3}>Pэ</p>
      </div>

      {sirensList.map(it => (
        <div
          key={`${it.type}_${it.inputPower}`}
          className={classes.row}
          onClick={() => dispatch(setSirene(it))}
        >
          <p className={classes.cell1}>{it.type}</p>
          <p className={classes.cell2}>{it.sensitivity}</p>
          <p className={classes.cell3}>{it.inputPower}</p>
        </div>
      ))} */}
      </div>
    </div>
  );
};

export default SelectSiren;
