import React from 'react';
import PropTypes from 'prop-types';

import LCDSymbolSVG from 'components/EditorComponents/LCDSymbolSVG/LCDSymbolSVG.js';

import classes from './LCDLine1.module.scss';

const line1 = `АКТИВНОСТИ НЕТ                `;

class LCDLine1 extends React.PureComponent {
  render() {
    const textLine = this.props.textLine || line1;

    return (
      <div className={classes.textRow}>
        {/* 24 characters */
        textLine
          .slice(0, 24)
          .split(``)
          .map((it, i) => (
            <div key={`${it}_${i}`} className={classes.textRowItem}>
              <LCDSymbolSVG LCDSymbol={it} />
            </div>
          ))}
      </div>
    );
  }
}

LCDLine1.propTypes = {
  textLine: PropTypes.string
};

export default LCDLine1;
