import React from 'react';
import PropTypes from 'prop-types';

import Typography from '@material-ui/core/Typography';

import LCDMeta17820 from 'components/EditorComponents/LCDMeta17820/LCDMeta17820.js';
import PanelLED from './PanelLED/PanelLED.js';
import PanelControls from './PanelControls/PanelControls.js';

import Logo from 'assets/img/logo_black-2.png';

import classes from './Meta17820.module.scss';

class Meta17820 extends React.Component {
  onPanelControlsClick = field => {
    if (field === `К1`) {
      const item = this.props.item;
      item.LEDs[3].on = !item.LEDs[3].on;

      this.props.onChange(this.props.item.id, item);
    }
  };

  render() {
    const { item, LCDprops } = this.props;

    const lineStyle = {
      top: item.top || ``,
      left: item.left || ``
    };

    return (
      <div className={classes.main} style={lineStyle}>
        <img
          className={classes.logo}
          src={Logo}
          title="logo"
          alt="Системы оповещения Meta"
        />

        <div className={classes.panel_text}>
          <Typography variant="body1" align="center">
            {item.textName1}
          </Typography>
          <Typography variant="body1" align="center">
            {item.textName2}
          </Typography>
        </div>

        <div className={classes.panel}>
          <div className={classes.panel_control}>
            <div className={classes.panel_LCD}>
              <LCDMeta17820 LCDprops={LCDprops} />
            </div>

            <PanelControls
              controls={item.controls}
              onClick={this.onPanelControlsClick}
            />
          </div>
          <PanelLED LEDs={item.LEDs} />
        </div>
      </div>
    );
  }
}

Meta17820.propTypes = {
  item: PropTypes.object,
  LCDprops: PropTypes.object,
  onChange: PropTypes.func.isRequired
};

export default Meta17820;
