import React from 'react';
import PropTypes from 'prop-types';

import LCDSymbolSVG from 'components/EditorComponents/LCDSymbolSVG/LCDSymbolSVG.js';

import classes from './LCDLine2.module.scss';

const line2 = `ЗОНЫ: 1 2 3 4 5 6 7 8         `;

class LCDLine2 extends React.PureComponent {
  render() {
    const textLine = this.props.textLine || line2;
    return (
      <div className={classes.textRow}>
        {/* 24 characters */
        textLine
          .slice(0, 24)
          .split(``)
          .map((it, i) => (
            <div key={`${it}_${i}`} className={classes.textRowItem}>
              <LCDSymbolSVG LCDSymbol={it} />
            </div>
          ))}
      </div>
    );
  }
}

LCDLine2.propTypes = {
  textLine: PropTypes.string
};

export default LCDLine2;
