import React from 'react';
import PropTypes from 'prop-types';

import { LCDSymbolsArr } from 'utils/const/LCdSymbols/LCDSymbols.js';

const sizeSquare = 2;
const rowsNumber = 8;
const colsNumber = 5;

class LCDSymbolSVG extends React.PureComponent {
  render() {
    const LCDSymbol = LCDSymbolsArr[this.props.LCDSymbol] || LCDSymbolsArr[` `];
    return (
      <svg height={sizeSquare * rowsNumber} width={sizeSquare * colsNumber}>
        {LCDSymbol.map((it, i) =>
          it.map((item, j) => (
            <rect
              key={`${i}_${j}`}
              width={sizeSquare}
              height={sizeSquare}
              x={i * sizeSquare}
              y={j * sizeSquare}
              fill={item === `1` ? `black` : `white`}
            />
          ))
        )}
      </svg>
    );
  }
}

LCDSymbolSVG.propTypes = {
  LCDSymbol: PropTypes.string
};

export default LCDSymbolSVG;
