import React from 'react';
import PropTypes from 'prop-types';

import LCDSymbolSVG from 'components/EditorComponents/LCDSymbolSVG/LCDSymbolSVG.js';

import classes from './LCDLine0.module.scss';

class LCDLine0 extends React.PureComponent {
  constructor(props) {
    super(props);

    const now = new Date();
    const time = {
      h: now.getHours(),
      m: now.getMinutes(),
      s: now.getSeconds(),
      d: now.getDate(),
      month: now.getMonth() + 1,
      y: now.getFullYear() % 100
    };
    if (time.s < 10) time.s = `0${time.s}`;
    if (time.h < 10) time.h = `0${time.h}`;
    if (time.m < 10) time.m = `0${time.m}`;
    if (time.d < 10) time.d = `0${time.d}`;
    if (time.month < 10) time.month = `0${time.month}`;

    this.state = {
      time,
      textLine: this.getLine1(time)
    };
  }
  componentDidMount() {
    this.lastTimerID = setTimeout(() => {
      this.setTime();
    }, 1000);
  }
  componentWillUnmount() {
    clearTimeout(this.lastTimerID);
  }
  getLine1(time) {
    return `ЦБ  ${time.h}:${time.m}:${time.s} ${time.d}.${time.month}.${time.y}         `;
  }
  setTime() {
    const { time } = this.state;
    const now = new Date();

    let s = now.getSeconds();
    if (s < 10) s = `0${s}`;

    if (time.s !== s) {
      time.s = s;

      if (s === `00`) {
        time.h = now.getHours();
        time.m = now.getMinutes();
        if (time.h < 10) time.h = `0${time.h}`;
        if (time.m < 10) time.m = `0${time.m}`;

        if (time.m === `00`) {
          time.d = now.getDate();
          time.month = now.getMonth() + 1;
          time.y = now.getFullYear() % 100;

          if (time.d < 10) time.d = `0${time.d}`;
          if (time.month < 10) time.month = `0${time.month}`;
        }
      }

      const textLine = this.getLine1(time);

      this.setState({ textLine, time });
    }

    this.lastTimerID = setTimeout(() => {
      this.setTime();
    }, 100);
  }
  render() {
    const textLine = this.props.textLine || this.state.textLine;

    return (
      <div className={classes.textRow}>
        {/* 24 characters */
        textLine
          .slice(0, 24)
          .split(``)
          .map((it, i) => (
            <div key={`${it}_${i}`} className={classes.textRowItem}>
              <LCDSymbolSVG LCDSymbol={it} />
            </div>
          ))}
      </div>
    );
  }
}

LCDLine0.propTypes = {
  textLine: PropTypes.string
};

export default LCDLine0;
