import React from 'react';
import { connect } from 'react-redux';
import { Typography, TextField, ButtonBase } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import ActivePaperBtn from 'components/UI/ActivePaperBtn/ActivePaperBtn';
import SelectSiren from 'pages/PageCalcSound/SelectSiren/SelectSiren';
import classes from './PanelMeta17820.module.scss';

const numberBlocks = [
  {
    title: `1`
  },
  {
    title: `2`
  },
  {
    title: `3`
  },
  {
    title: `4`
  },
  {
    title: `5`
  }
];

const numberPults = [
  {
    title: `1`
  },
  {
    title: `2`
  }
];

const CssTextField = withStyles({
  root: {
    '& fieldset': {
      borderColor: '#2196f3',
      borderWidth: 1
    },
    '& .MuiOutlinedInput-root': {
      '&:hover fieldset': {
        borderColor: '#2196f3',
        borderWidth: 2
      }
    }
  }
})(TextField);

class PanelMeta17820 extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activePowerBlock: `200Вт`,
      activeNumberBlock: `2`,
      activeNumberPult: `2`,
      activeNumberSirene: '1',
      showStepSetNumberBlocks: false,
      showStepSetNumberPults: false
    };
  }

  onActivePowerBlockChange = activePowerBlock => {
    this.setState({
      activePowerBlock
    });
  };
  onActiveNumberBlockChange = activeNumberBlock => {
    this.setState({
      activeNumberBlock
    });
  };
  onActiveNumberPultChange = activeNumberPult => {
    this.setState({
      activeNumberPult
    });
  };

  onActiveNumberSireneChange = e => {
    const value = +e.target.value;
    if (value < 0) return;

    this.setState({ activeNumberSirene: String(value) });
  };

  render() {
    const {
      activePowerBlock,
      activeNumberBlock,
      activeNumberPult,
      activeNumberSirene
    } = this.state;

    const { activeBlock, sirene } = this.props;
    const isSetSirene =
      sirene && sirene.type && sirene.inputPower && sirene.sensitivity;
    const P = activeBlock === 'МЕТА 17820' ? '200' : '500';

    return (
      <div className={classes.main}>
        <div className={classes.block}>
          <Typography variant="body1" gutterBottom>
            У блока <b>{activeBlock}</b> номинальная выходная мощность
            <b> {P}</b>Вт
          </Typography>
        </div>

        {activePowerBlock && (
          <div className={classes.block}>
            <p className={classes.blockTitle}>
              Выберите количество блоков в системе
            </p>
            <Typography variant="body1" gutterBottom>
              (1 блок дает 8 линий речевого оповещения)
            </Typography>
            <div className={classes.blockStep}>
              {numberBlocks.map(it => (
                <ActivePaperBtn
                  key={it.title}
                  isActive={activeNumberBlock === it.title}
                  {...it}
                  width="70px"
                  onChange={this.onActiveNumberBlockChange}
                />
              ))}
            </div>
            <Typography variant="body1" gutterBottom>
              Количетсво линий речевого оповещения {activeNumberBlock * 8}
            </Typography>
          </div>
        )}

        {activeNumberBlock && (
          <div className={classes.block}>
            <p className={classes.blockTitle}>Выберите количество пультов</p>
            <div className={classes.blockStep}>
              {numberPults.map(it => (
                <ActivePaperBtn
                  key={it.title}
                  isActive={activeNumberPult === it.title}
                  {...it}
                  width="70px"
                  onChange={this.onActiveNumberPultChange}
                />
              ))}
            </div>
          </div>
        )}

        {activeNumberPult && (
          <div className={classes.block}>
            <p className={classes.blockTitle}>Выберите тип оповещателя</p>
            <SelectSiren />
          </div>
        )}

        {isSetSirene && (
          <div className={classes.block}>
            <p className={classes.blockTitle}>
              Выберите количество оповещателей в системе
            </p>
            <div className={classes.blockStep}>
              <CssTextField
                style={{ width: 120 }}
                label="Кол-во"
                value={activeNumberSirene}
                type="number"
                margin="dense"
                variant="outlined"
                onChange={this.onActiveNumberSireneChange}
              />
            </div>
          </div>
        )}

        {activeNumberSirene && (
          <div className={classes.panelBtn}>
            <ButtonBase
              focusRipple
              className={classes.setSirensBtn}
              // onClick={handleSetSireneClick}
            >
              <p>Показать схему</p>
            </ButtonBase>

            <ButtonBase
              focusRipple
              className={classes.setSirensBtn}
              // onClick={handleSetSireneClick}
            >
              <p>Скачать спецификацию</p>
            </ButtonBase>
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  sirene: state.sirene.sirene
});

export default connect(mapStateToProps)(PanelMeta17820);
