import React from 'react';
import { connect } from 'react-redux';
import { Link, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import { setShowAsideMenu } from 'store/actions/appSettings.js';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Drawer from '@material-ui/core/Drawer';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

import {
  ChevronLeft,
  AccountBox,
  Input,
  SwapHoriz,
  Dashboard,
  FileCopy,
  Speaker,
  ListAlt
} from '@material-ui/icons';

import { MENU_LINKS } from 'utils/const/menuLinks.js';
import logo from 'assets/img/logo_fill-2.png';

import classes from './AsideMenu.module.scss';

const iconList = {
  AccountBox: <AccountBox />,
  Input: <Input />,
  SwapHoriz: <SwapHoriz />,
  Dashboard: <Dashboard />,
  FileCopy: <FileCopy />,
  Speaker: <Speaker />,
  ListAlt: <ListAlt />
};

const MuneListD = ({ location }) => {
  const { pathname } = location;

  return (
    <div className={classes.list}>
      <img
        className={classes.logo}
        src={logo}
        title="logo"
        alt="Системы оповещения Meta"
      />
      <List dense component="nav">
        {MENU_LINKS.map(it => (
          <ListItem
            key={it.text}
            button
            component={Link}
            selected={pathname === it.to}
            to={it.to}
          >
            <div className={classes.ListItemIcon}>
              {iconList[it.icon] || ``}
            </div>
            <ListItemText primary={it.text} />
          </ListItem>
        ))}
      </List>
    </div>
  );
};

const MuneList = withRouter(props => <MuneListD {...props} />);

const DesktopMenu = ({ showAsideMenu }) => {
  return (
    <Drawer
      variant="persistent"
      anchor="left"
      open={showAsideMenu}
      classes={{
        paper: classes.drawerPaper
      }}
    >
      <div className={classes.drawerHeader}></div>
      <MuneList />
    </Drawer>
  );
};

class AsideMenu extends React.Component {
  toggleDrawer = open => () => {
    this.props.setShowAsideMenu(open);
  };

  render() {
    return this.props.isDesktop ? (
      // desktop menu
      <DesktopMenu showAsideMenu={this.props.showAsideMenu} />
    ) : (
      // mobile menu
      <SwipeableDrawer
        open={this.props.showAsideMenu}
        onClose={this.toggleDrawer(false)}
        onOpen={this.toggleDrawer(true)}
      >
        <div
          tabIndex={0}
          role="button"
          onClick={this.toggleDrawer(false)}
          onKeyDown={this.toggleDrawer(false)}
        >
          <div className={classes.drawerHeader}>
            <IconButton onClick={this.onIconCloseClick}>
              <ChevronLeft />
            </IconButton>
          </div>
          <Divider />

          <MuneList />
        </div>
      </SwipeableDrawer>
    );
  }
}

const mapStateToProps = state => {
  return {
    showAsideMenu: state.appSettings.showAsideMenu,
    isDesktop: state.appSettings.isDesktop
  };
};

const mapDispatchToProps = dispatch => {
  return {
    setShowAsideMenu: showAsideMenu => dispatch(setShowAsideMenu(showAsideMenu))
  };
};

AsideMenu.propTypes = {
  showAsideMenu: PropTypes.bool.isRequired,
  isDesktop: PropTypes.bool.isRequired,
  setShowAsideMenu: PropTypes.func,
  location: PropTypes.object
};

const AsideMenuWithRouter = withRouter(props => <AsideMenu {...props} />);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AsideMenuWithRouter);
