import { SIRENE_SET_SIRENE } from '../actionTypes';

const initialState = {
  sirene: {
    type: 'АСР-06.3.6',
    inputPower: 6,
    sensitivity: 90
  }
};

export default (state = initialState, action) => {
  switch (action.type) {
    case SIRENE_SET_SIRENE:
      return {
        ...state,
        sirene: action.payload
      };
    default:
      return state;
  }
};
