import { AUTH_LOGOUT, AUTH_SUCCESS } from '../actionTypes';

export const authLogin = password => async dispatch => {
  const token = password;

  dispatch(authSuccess(token));
};

export const authRegister = password => async dispatch => {
  const token = password;

  dispatch(authSuccess(token));
};

export const userLogout = () => dispatch => dispatch(logout());

export const autoLogin = () => dispatch => {
  const token = localStorage.getItem(`token`);

  if (!token) {
    dispatch(logout());
  } else {
    dispatch(authSuccess(token));
  }
};

export const logout = () => {
  localStorage.removeItem(`token`);
  return {
    type: AUTH_LOGOUT
  };
};

export const authSuccess = token => {
  return {
    type: AUTH_SUCCESS,
    token
  };
};
