import React from 'react';
import { connect } from 'react-redux';
import { setSirene } from 'store/actions/sirene';
import LayoutPage from 'layout/LayoutPage/LayoutPage.js';
import { TextField, ButtonBase, Typography } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import classes from './PageCalcSound.module.scss';
import SelectSiren from './SelectSiren/SelectSiren';

const CssTextField = withStyles({
  root: {
    '& fieldset': {
      borderColor: '#2196f3',
      borderWidth: 1
    },
    '& .MuiOutlinedInput-root': {
      '&:hover fieldset': {
        borderColor: '#2196f3',
        borderWidth: 2
      }
    }
  }
})(TextField);

const CssTextFieldReadOnly = withStyles({
  root: {
    '& fieldset': {
      borderColor: '#888',
      borderWidth: 1
    },
    '& .MuiOutlinedInput-root': {
      '&:hover fieldset': {
        borderColor: '#888',
        borderWidth: 1
      },
      '&.Mui-focused fieldset': {
        borderColor: '#888',
        borderWidth: 1
      },
      '&.Mui-focused:hover fieldset': {
        borderColor: '#888',
        borderWidth: 1
      }
    }
  }
})(TextField);

const CssTextFieldResult = withStyles({
  root: {
    '& .MuiOutlinedInput-notchedOutline': {
      borderColor: '#008800',
      borderWidth: 2
    },
    '& .MuiOutlinedInput-root.Mui-error .MuiOutlinedInput-notchedOutline': {
      borderColor: '#880000'
    }
  }
})(TextField);

class PageCalcSound extends React.Component {
  constructor(props) {
    super(props);

    const Nsh = 75;
    const Nsum = 0;
    const N0 = 112;
    const Pe = 50;
    const Nzv = 0;
    const L = 20;
    const R = 0;
    const Lmax = 0;
    const Nzvr = 0;

    const input = {
      Nsh,
      Nsum,
      N0,
      Pe,
      Nzv,
      L,
      R,
      Lmax,
      Nzvr
    };

    this.state = {
      input: this.calcSound(input),
      showSireneList: false
    };
  }

  componentDidUpdate(prevProps) {
    if (prevProps.sirene !== this.props.sirene) {
      const { sirene } = this.props;

      if (
        !sirene ||
        typeof sirene !== 'object' ||
        (!sirene.sensitivity || !sirene.inputPower)
      ) {
        return;
      }

      const { input } = this.state;
      input.N0 = sirene.sensitivity;
      input.Pe = sirene.inputPower;

      this.setState({ input: this.calcSound(input) });
    }
  }

  // логарифм по основанию x
  getBaseLog(x, y) {
    return Math.log(y) / Math.log(x);
  }
  // логарифм по основанию 10
  LOG(x) {
    return Math.log(x) / Math.log(10);
  }
  ROUND(x) {
    return Math.round(x * 100) / 100;
  }

  calcSound = inputData => {
    const input = { ...inputData };
    input.Nsum = input.Nsh + 15;
    input.Nzv = this.ROUND(input.N0 + 10 * this.LOG(input.Pe));
    input.R = this.ROUND(20 * this.LOG(input.L));
    input.Lmax = this.ROUND(Math.pow(10, (input.Nzv - input.Nsum) / 20));
    input.Nzvr = this.ROUND(input.Nzv - input.R);

    Object.entries(input).map(([key, value]) => (input[key] = String(value)));

    return input;
  };

  handleBtnTypeSirensClick = () => {
    this.setState(prevState => ({
      showSireneList: !prevState.showSireneList
    }));
  };

  handleChange = name => e => {
    const value = +e.target.value;

    switch (name) {
      case `Nsh`: {
        if (value < 0) return;
        break;
      }
      default:
        break;
    }

    const { input } = this.state;
    input[name] = value;

    this.setState({ input: this.calcSound(input) });
  };

  render() {
    const { input, showSireneList } = this.state;

    return (
      <LayoutPage>
        <Typography variant="h1" gutterBottom>
          Расчета уровня звукового давления
        </Typography>

        <div className={classes.step}>
          <p className={classes.stepTitle}>
            1. Определение уровня звука постоянного шума
          </p>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextField
                style={{ width: 120 }}
                label="Nш"
                value={input.Nsh}
                type="number"
                margin="dense"
                variant="outlined"
                onChange={this.handleChange('Nsh')}
              />
              <p className={classes.stepRowInputP}>дБ</p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                Уровень звука постоянного шума
              </p>
              <p className={classes.stepRowTextP}>
                Для расчета необходимого уровня звукового давления создаваемого
                системой оповещения принимаем, в соответствии с ГОСТ 12.1.036-81
                «Шум. Допустимые уровни в жилых и общественных зданиях», СН
                2.2.4/2.1.8.562-96 «Шум на рабочих местах, в помещениях жилых,
                общественных зданий и на территории жилой застройки» и СП
                51.13330.2011 "Защита от шума"
              </p>
            </div>
          </div>
        </div>

        <div className={classes.step}>
          <p className={classes.stepTitle}>
            2. Определение необходимого уровня звукового давления
          </p>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextFieldReadOnly
                style={{ width: 120 }}
                InputProps={{
                  readOnly: true
                }}
                label="Nсум"
                value={input.Nsum}
                margin="dense"
                variant="outlined"
              />
              <p className={classes.stepRowInputP}>дБ</p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                Необходимый уровень звукового давления (уровень постоянного шума
                +15дБ)
              </p>
              <p className={classes.stepRowTextP}>
                На основании п. 4.2. СП 3.13130.2009 необходимо обеспечить
                уровень звукового давления не менее чем на 15дБ выше допустимого
                уровня звука постоянного шума в защищаемом помещении.
              </p>
            </div>
          </div>
        </div>

        <div className={classes.step}>
          <p className={classes.stepTitle}>
            3. Определение уровня звукового давления создаваемого оповещателем
            при подведении определенной мощности
          </p>

          {showSireneList ? (
            <SelectSiren handleCancelBtnClick={this.handleBtnTypeSirensClick} />
          ) : (
            <div className={classes.setSirens}>
              <ButtonBase
                focusRipple
                className={classes.setSirensBtn}
                onClick={this.handleBtnTypeSirensClick}
              >
                <p>Выбрать тип оповещетеля</p>
              </ButtonBase>
            </div>
          )}

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextField
                style={{ width: 120 }}
                label="N0"
                value={input.N0}
                type="number"
                margin="dense"
                variant="outlined"
                onChange={this.handleChange('N0')}
              />
              <p className={classes.stepRowInputP}>дБ</p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                Чувствительность громкоговорителя
              </p>
              <p className={classes.stepRowTextP}>
                Уровень звукового давления на расстоянии 1м при подаваемой
                мощности 1 Вт, паспортная величина
              </p>
            </div>
          </div>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextField
                style={{ width: 120 }}
                label="Pэ"
                value={input.Pe}
                type="number"
                margin="dense"
                variant="outlined"
                onChange={this.handleChange('Pe')}
              />
              <p className={classes.stepRowInputP}>дБ</p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                Электрическая мощность применяемого оповещателя
              </p>
              <p className={classes.stepRowTextP}>паспортная величина</p>
            </div>
          </div>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextFieldReadOnly
                style={{ width: 120 }}
                InputProps={{
                  readOnly: true
                }}
                label="Nзв"
                value={input.Nzv}
                margin="dense"
                variant="outlined"
              />
              <p className={classes.stepRowInputP}>дБ</p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                Уровень звукового давления создаваемого оповещателем
              </p>
            </div>
          </div>
        </div>

        <div className={classes.step}>
          <p className={classes.stepTitle}>
            4. Расчет величины ослабления звукового давления в удаленной
            расчетной точке на заданном расстоянии
          </p>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextField
                style={{ width: 120 }}
                label="L"
                value={input.L}
                type="number"
                margin="dense"
                variant="outlined"
                onChange={this.handleChange('L')}
              />
              <p className={classes.stepRowInputP}>м</p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                Расстояние от оповещателя до расчетной точки
              </p>
              <p className={classes.stepRowTextP}>
                «расчётная точка» - точка в которой необходимо знать уровень
                звукового давления, производимого оповещателем.
              </p>
            </div>
          </div>
          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextFieldReadOnly
                style={{ width: 120 }}
                InputProps={{
                  readOnly: true
                }}
                label="R"
                value={input.R}
                margin="dense"
                variant="outlined"
              />
              <p className={classes.stepRowInputP}>дБ</p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                Ослабление уровня звукового давления на данном расстоянии
              </p>
            </div>
          </div>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextFieldReadOnly
                style={{ width: 120 }}
                InputProps={{
                  readOnly: true
                }}
                label="Lмакс"
                value={input.Lmax}
                margin="dense"
                variant="outlined"
              />
              <p className={classes.stepRowInputP}>м</p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                Предельное расстояние от оповещателя
              </p>
              <p className={classes.stepRowTextP}>
                На этом расстоянии обеспечивается выполнение условия "не менее
                чем на 15 дБА выше допустимого уровня звука постоянного шума в
                защищаемом помещении" (СП3.13130.2009 п.4.2)
              </p>
            </div>
          </div>
        </div>

        <div className={classes.step}>
          <p className={classes.stepTitle}>
            5. Расчет уровня звукового давления в удаленной точке
          </p>

          <div className={classes.stepRow}>
            <div className={classes.stepRowInput}>
              <CssTextFieldResult
                className={classes.margin}
                label="Nзв р"
                error={input.Nzvr < input.Nsum}
                value={input.Nzvr}
                margin="dense"
                variant="outlined"
              />
              <p className={classes.stepRowInputP}>дБ</p>
            </div>
            <div className={classes.stepRowText}>
              <p className={classes.stepRowTextTitle}>
                Значение уровня звукового давления на данном расстоянии
              </p>
            </div>
          </div>
        </div>

        {input.Nzvr >= input.Nsum ? (
          <div className={classes.stepGreen}>
            <p>Уровень звука достаточный</p>
            <p>Мощность громкоговорителя удовлетворяет СП 3.13130.2009</p>
          </div>
        ) : (
          <div className={classes.stepRed}>
            <p>Недостаточный уровень звука</p>
            <p>Необходимо увеличить мощность включения громкоговорителя</p>
          </div>
        )}
      </LayoutPage>
    );
  }
}

const mapStateToProps = state => ({
  sirene: state.sirene.sirene
});

const mapDispatchToProps = {
  setSirene
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PageCalcSound);
